import Vue from 'vue';
import i18n from '../../../i18n';
import XColumn from './deck.column.vue';
export default Vue.extend({
    i18n: i18n(),
    components: {
        XColumn,
    },
    data() {
        return {
            note: null,
            fetching: true
        };
    },
    watch: {
        $route: 'fetch'
    },
    created() {
        this.fetch();
    },
    methods: {
        fetch() {
            this.fetching = true;
            this.$root.api('notes/show', {
                noteId: this.$route.params.note
            }).then(note => {
                this.note = note;
                this.fetching = false;
            });
        }
    }
});
